import {getFollow, concern} from '../../api/api.js';
export default {
	data() {
		return {
			followList: '',
		}
	},
	
	methods: {
		getAllFollow() {
			// console.log("请求开始")
			getFollow().then(data => {
				this.followList = data;
				console.log(this.followList)
			});
		},
		userInfo(id) {
			uni.navigateTo({
				url: '/pages/daily/userInfo?uid=' + id
			});
		},
		unFollow(id,index) {
				concern(2, id).then(data => {
					this.followList.splice(index,1)
				});
			}
			
		
	},
	onLoad() {
		this.getAllFollow()
	}
}
